.modal-close{display:none}.modal-close:before{content:"";background:#000;position:fixed;left:0;top:0;width:100%;height:100%;opacity:.7;z-index:60;cursor:default}.modal-close__inner{width:400px;padding:35px;background:#fff;border-radius:5px;position:fixed;z-index:90;top:50%;left:50%;-webkit-transform:translate(-50%,-50%);-ms-transform:translate(-50%,-50%);transform:translate(-50%,-50%)}@media (max-width:767px){.modal-close__inner{padding:25px;width:calc(100vw - 40px)}}.modal-close__title{font-weight:600;font-size:22px;line-height:30px;color:#4b5561;margin-bottom:25px;text-align:center}@media (max-width:1279px){.modal-close__title{font-size:20px}}@media (max-width:1023px){.modal-close__title{font-size:18px;line-height:25px}}@media (max-width:767px){.modal-close__title{font-size:16px;margin-bottom:18px}}.modal-close__buttons{display:-webkit-box;display:-ms-flexbox;display:flex;-webkit-box-align:center;-ms-flex-align:center;align-items:center}.modal-close__button{width:50%;margin-right:20px}.modal-close__button:last-child{margin:0}@media (max-width:767px){.modal-close__button{margin-right:10px}}