.checkbox{position:relative;cursor:pointer;display:-webkit-box;display:-ms-flexbox;display:flex;-webkit-box-align:center;-ms-flex-align:center;align-items:center;-webkit-user-select:none;-moz-user-select:none;-ms-user-select:none;user-select:none}.checkbox__square{width:16px;height:16px;background:#d2e1f2;border-radius:3px;position:relative}.checkbox__square,.checkbox__square:after{-webkit-transition:.2s;-o-transition:.2s;transition:.2s}.checkbox__square:after{content:"";width:11px;height:11px;background:url(/src/blocks/header/assets/img/checked.svg);background-size:contain!important;background-repeat:no-repeat!important;background-position:50%!important;position:absolute;top:8px;right:-3px;-webkit-transform:translateY(-50%) translateX(-50%);-ms-transform:translateY(-50%) translateX(-50%);transform:translateY(-50%) translateX(-50%);opacity:0}.checkbox__input-checkbox{opacity:0;position:absolute;width:100%;height:100%;top:0;right:0;bottom:0;left:0;z-index:2;cursor:pointer}.checkbox__input-checkbox:checked+.checkbox__square{background:#1c93e2}.checkbox__input-checkbox:checked+.checkbox__square:after{opacity:1}